*,
*:before,
*:after {
  box-sizing: border-box;
}

html{
  width: 100%;
  max-width: 100%;
  //overflow-x: hidden;
}

body{
  padding: 0;
  margin: 0;
  font-family: 'League Spartan', sans-serif;
  font-size: 18px;
  line-height: 30px;
  background-color: $deep-blue;
  color: white;
  width: 100%;
  max-width: 100%;
  //overflow-x: hidden;
}

.container{
  max-width: 1270px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
}

h1, h2 {
  margin: 0;
}

ol,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  display: block;
}